@mixin mq($breakpoint) {
  // @if $breakpoint == small {
  //   @media screen and (min-width: 30em) {
  //     @content;
  //   }
  // }
  @if $breakpoint == md {
    @media screen and (min-width: 40em) {
      @content;
    }
  }

  @if $breakpoint == home {
    @media screen and (min-width: 56.25em) {
      @content;
    }
  }

  @if $breakpoint == lg {
    @media screen and (min-width: 80em) {
      @content;
    }
  }
}
